"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'

interface Report {
  id: string
  title: string
  category: string
  status: 'urgent' | 'important' | 'moderate' | 'low' | 'resolved'
  location: string
  coordinates: { lat: number; lng: number }
  description: string
  date: string
  votes: number
}

const mockReports: Report[] = [
  {
    id: '1',
    title: 'Bache en Av. Reforma',
    category: 'Infraestructura',
    status: 'urgent',
    location: 'Av. Reforma #456',
    coordinates: { lat: 19.4326, lng: -99.1332 },
    description: 'Bache grande que causa daños a vehículos',
    date: '2024-01-15',
    votes: 23
  },
  {
    id: '2',
    title: 'Falta de iluminación',
    category: 'Seguridad',
    status: 'important',
    location: 'Calle Juárez #123',
    coordinates: { lat: 19.4285, lng: -99.1277 },
    description: 'Zona muy oscura por las noches',
    date: '2024-01-14',
    votes: 18
  },
  {
    id: '3',
    title: 'Basura acumulada',
    category: 'Medio Ambiente',
    status: 'moderate',
    location: 'Parque Central',
    coordinates: { lat: 19.4367, lng: -99.1401 },
    description: 'Acumulación de basura en el parque',
    date: '2024-01-13',
    votes: 12
  },
  {
    id: '4',
    title: 'Semáforo descompuesto',
    category: 'Transporte',
    status: 'resolved',
    location: 'Cruce Insurgentes',
    coordinates: { lat: 19.4247, lng: -99.1353 },
    description: 'Semáforo intermitente causa congestionamiento',
    date: '2024-01-12',
    votes: 31
  }
]

const statusColors = {
  urgent: 'bg-red-500',
  important: 'bg-orange-500',
  moderate: 'bg-yellow-500',
  low: 'bg-green-500',
  resolved: 'bg-gray-500'
}

const statusLabels = {
  urgent: 'Urgente',
  important: 'Importante',
  moderate: 'Moderado',
  low: 'Bajo',
  resolved: 'Resuelto'
}

export function InteractiveMap() {
  const [reports, setReports] = useState<Report[]>(mockReports)
  const [selectedReport, setSelectedReport] = useState<Report | null>(null)
  const [filterStatus, setFilterStatus] = useState<string>('all')
  const [filterCategory, setFilterCategory] = useState<string>('all')

  const categories = ['Infraestructura', 'Seguridad', 'Servicios Públicos', 'Medio Ambiente', 'Transporte', 'Salud', 'Educación']

  const filteredReports = reports.filter(report => {
    const statusMatch = filterStatus === 'all' || report.status === filterStatus
    const categoryMatch = filterCategory === 'all' || report.category === filterCategory
    return statusMatch && categoryMatch
  })

  const handleReportClick = (report: Report) => {
    setSelectedReport(report)
  }

  const handleVote = (reportId: string) => {
    setReports(prev => prev.map(report => 
      report.id === reportId 
        ? { ...report, votes: report.votes + 1 }
        : report
    ))
  }

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
      {/* Map Area */}
      <div className="lg:col-span-2">
        <Card>
          <CardHeader>
            <CardTitle>Mapa Interactivo de Reportes</CardTitle>
            <CardDescription>
              Visualiza todos los reportes de la comunidad por ubicación
            </CardDescription>
            
            {/* Filters */}
            <div className="flex flex-wrap gap-4 pt-4">
              <div className="flex-1 min-w-[200px]">
                <Select value={filterStatus} onValueChange={setFilterStatus}>
                  <SelectTrigger>
                    <SelectValue placeholder="Filtrar por estado" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Todos los estados</SelectItem>
                    <SelectItem value="urgent">Urgente</SelectItem>
                    <SelectItem value="important">Importante</SelectItem>
                    <SelectItem value="moderate">Moderado</SelectItem>
                    <SelectItem value="low">Bajo</SelectItem>
                    <SelectItem value="resolved">Resuelto</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              
              <div className="flex-1 min-w-[200px]">
                <Select value={filterCategory} onValueChange={setFilterCategory}>
                  <SelectTrigger>
                    <SelectValue placeholder="Filtrar por categoría" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Todas las categorías</SelectItem>
                    {categories.map(category => (
                      <SelectItem key={category} value={category}>
                        {category}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            {/* Simulated Map */}
            <div className="relative bg-gradient-to-br from-green-100 to-blue-100 rounded-lg h-96 overflow-hidden">
              <div className="absolute inset-0 bg-gray-200 opacity-20"></div>
              
              {/* Map Legend */}
              <div className="absolute top-4 left-4 bg-white p-3 rounded-lg shadow-md z-10">
                <h4 className="font-semibold text-sm mb-2">Leyenda</h4>
                <div className="space-y-1">
                  {Object.entries(statusLabels).map(([status, label]) => (
                    <div key={status} className="flex items-center gap-2 text-xs">
                      <div className={`w-3 h-3 rounded-full ${statusColors[status as keyof typeof statusColors]}`}></div>
                      <span>{label}</span>
                    </div>
                  ))}
                </div>
              </div>

              {/* Map Points */}
              {filteredReports.map((report, index) => (
                <div
                  key={report.id}
                  className={`absolute w-4 h-4 rounded-full cursor-pointer transform -translate-x-2 -translate-y-2 hover:scale-125 transition-transform ${statusColors[report.status]} shadow-lg`}
                  style={{
                    left: `${20 + (index * 15) % 60}%`,
                    top: `${30 + (index * 20) % 40}%`
                  }}
                  onClick={() => handleReportClick(report)}
                  title={report.title}
                >
                  <div className="absolute -top-8 left-1/2 transform -translate-x-1/2 bg-black text-white text-xs px-2 py-1 rounded opacity-0 hover:opacity-100 transition-opacity whitespace-nowrap">
                    {report.title}
                  </div>
                </div>
              ))}

              {/* Mexico City Outline Simulation */}
              <div className="absolute inset-0 flex items-center justify-center">
                <div className="text-6xl text-gray-300 font-bold opacity-50">
                  CDMX
                </div>
              </div>
            </div>

            {/* Map Statistics */}
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-4">
              <div className="text-center">
                <p className="text-2xl font-bold text-red-600">
                  {filteredReports.filter(r => r.status === 'urgent').length}
                </p>
                <p className="text-sm text-gray-600">Urgentes</p>
              </div>
              <div className="text-center">
                <p className="text-2xl font-bold text-orange-600">
                  {filteredReports.filter(r => r.status === 'important').length}
                </p>
                <p className="text-sm text-gray-600">Importantes</p>
              </div>
              <div className="text-center">
                <p className="text-2xl font-bold text-yellow-600">
                  {filteredReports.filter(r => r.status === 'moderate').length}
                </p>
                <p className="text-sm text-gray-600">Moderados</p>
              </div>
              <div className="text-center">
                <p className="text-2xl font-bold text-gray-600">
                  {filteredReports.filter(r => r.status === 'resolved').length}
                </p>
                <p className="text-sm text-gray-600">Resueltos</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Report Details Sidebar */}
      <div className="space-y-4">
        {selectedReport ? (
          <Card>
            <CardHeader>
              <div className="flex items-start justify-between">
                <div>
                  <CardTitle className="text-lg">{selectedReport.title}</CardTitle>
                  <CardDescription>{selectedReport.location}</CardDescription>
                </div>
                <Badge 
                  variant="secondary" 
                  className={`${statusColors[selectedReport.status]} text-white`}
                >
                  {statusLabels[selectedReport.status]}
                </Badge>
              </div>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <p className="text-sm font-medium text-gray-700">Categoría</p>
                <p className="text-sm">{selectedReport.category}</p>
              </div>
              
              <div>
                <p className="text-sm font-medium text-gray-700">Descripción</p>
                <p className="text-sm">{selectedReport.description}</p>
              </div>
              
              <div>
                <p className="text-sm font-medium text-gray-700">Fecha de reporte</p>
                <p className="text-sm">{new Date(selectedReport.date).toLocaleDateString('es-MX')}</p>
              </div>
              
              <div className="flex items-center justify-between pt-4 border-t">
                <div className="flex items-center gap-2">
                  <span className="text-sm font-medium">{selectedReport.votes}</span>
                  <span className="text-sm text-gray-600">votos</span>
                </div>
                <Button 
                  size="sm" 
                  onClick={() => handleVote(selectedReport.id)}
                  className="bg-blue-600 hover:bg-blue-700"
                >
                  Votar
                </Button>
              </div>
            </CardContent>
          </Card>
        ) : (
          <Card>
            <CardContent className="pt-6">
              <div className="text-center text-gray-500">
                <p className="text-sm">Selecciona un punto en el mapa</p>
                <p className="text-sm">para ver los detalles del reporte</p>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Recent Reports */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Reportes Recientes</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              {filteredReports.slice(0, 3).map((report) => (
                <div 
                  key={report.id}
                  className="flex items-start gap-3 p-2 rounded-lg hover:bg-gray-50 cursor-pointer"
                  onClick={() => handleReportClick(report)}
                >
                  <div className={`w-3 h-3 rounded-full mt-1 ${statusColors[report.status]}`}></div>
                  <div className="flex-1 min-w-0">
                    <p className="text-sm font-medium truncate">{report.title}</p>
                    <p className="text-xs text-gray-500">{report.category}</p>
                    <p className="text-xs text-gray-400">{report.votes} votos</p>
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
