"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Progress } from '@/components/ui/progress'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'

interface DashboardData {
  totalReports: number
  resolvedReports: number
  activeProposals: number
  totalVotes: number
  categoryStats: { category: string; count: number; percentage: number }[]
  statusStats: { status: string; count: number; color: string }[]
  monthlyTrends: { month: string; reports: number; proposals: number }[]
  topProposals: { title: string; votes: number; category: string }[]
  recentActivity: { type: string; title: string; time: string; status: string }[]
}

const mockDashboardData: DashboardData = {
  totalReports: 1247,
  resolvedReports: 892,
  activeProposals: 23,
  totalVotes: 5634,
  categoryStats: [
    { category: 'Infraestructura', count: 342, percentage: 27.4 },
    { category: 'Seguridad', count: 298, percentage: 23.9 },
    { category: 'Servicios Públicos', count: 234, percentage: 18.8 },
    { category: 'Medio Ambiente', count: 187, percentage: 15.0 },
    { category: 'Transporte', count: 123, percentage: 9.9 },
    { category: 'Otros', count: 63, percentage: 5.0 }
  ],
  statusStats: [
    { status: 'Resuelto', count: 892, color: 'bg-green-500' },
    { status: 'En Proceso', count: 234, color: 'bg-blue-500' },
    { status: 'Pendiente', count: 89, color: 'bg-yellow-500' },
    { status: 'Urgente', count: 32, color: 'bg-red-500' }
  ],
  monthlyTrends: [
    { month: 'Ene', reports: 98, proposals: 12 },
    { month: 'Feb', reports: 112, proposals: 15 },
    { month: 'Mar', reports: 134, proposals: 18 },
    { month: 'Abr', reports: 156, proposals: 22 },
    { month: 'May', reports: 143, proposals: 19 },
    { month: 'Jun', reports: 167, proposals: 25 }
  ],
  topProposals: [
    { title: 'Mejora de iluminación en parques', votes: 203, category: 'Seguridad' },
    { title: 'Instalación de ciclovías', votes: 156, category: 'Transporte' },
    { title: 'Programa de reciclaje', votes: 134, category: 'Medio Ambiente' },
    { title: 'Centro comunitario', votes: 98, category: 'Servicios Públicos' }
  ],
  recentActivity: [
    { type: 'Reporte', title: 'Bache en Av. Reforma', time: 'Hace 2 horas', status: 'Nuevo' },
    { type: 'Propuesta', title: 'Instalación de semáforos', time: 'Hace 4 horas', status: 'Aprobada' },
    { type: 'Reporte', title: 'Falta de iluminación', time: 'Hace 6 horas', status: 'En proceso' },
    { type: 'Propuesta', title: 'Parque para mascotas', time: 'Hace 8 horas', status: 'Nueva' },
    { type: 'Reporte', title: 'Basura acumulada', time: 'Hace 12 horas', status: 'Resuelto' }
  ]
}

export function Dashboard() {
  const [data, setData] = useState<DashboardData>(mockDashboardData)
  const [activeTab, setActiveTab] = useState('overview')

  // Simulate real-time updates
  useEffect(() => {
    const interval = setInterval(() => {
      setData(prev => ({
        ...prev,
        totalReports: prev.totalReports + Math.floor(Math.random() * 3),
        totalVotes: prev.totalVotes + Math.floor(Math.random() * 5)
      }))
    }, 10000) // Update every 10 seconds

    return () => clearInterval(interval)
  }, [])

  const resolutionRate = (data.resolvedReports / data.totalReports) * 100

  return (
    <div className="space-y-6">
      <Tabs value={activeTab} onValueChange={setActiveTab}>
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="overview">Resumen General</TabsTrigger>
          <TabsTrigger value="analytics">Análisis</TabsTrigger>
          <TabsTrigger value="activity">Actividad Reciente</TabsTrigger>
        </TabsList>

        <TabsContent value="overview" className="space-y-6">
          {/* Key Metrics */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Total Reportes</CardTitle>
                <div className="h-4 w-4 text-blue-600">📊</div>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{data.totalReports.toLocaleString()}</div>
                <p className="text-xs text-muted-foreground">
                  +12% desde el mes pasado
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Tasa de Resolución</CardTitle>
                <div className="h-4 w-4 text-green-600">✅</div>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{resolutionRate.toFixed(1)}%</div>
                <p className="text-xs text-muted-foreground">
                  {data.resolvedReports} de {data.totalReports} resueltos
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Propuestas Activas</CardTitle>
                <div className="h-4 w-4 text-purple-600">💡</div>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{data.activeProposals}</div>
                <p className="text-xs text-muted-foreground">
                  En proceso de votación
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Total Votos</CardTitle>
                <div className="h-4 w-4 text-orange-600">🗳️</div>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{data.totalVotes.toLocaleString()}</div>
                <p className="text-xs text-muted-foreground">
                  Participación ciudadana
                </p>
              </CardContent>
            </Card>
          </div>

          {/* Status Distribution */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            <Card>
              <CardHeader>
                <CardTitle>Estado de Reportes</CardTitle>
                <CardDescription>Distribución actual por estado</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                {data.statusStats.map((stat) => (
                  <div key={stat.status} className="flex items-center justify-between">
                    <div className="flex items-center gap-3">
                      <div className={`w-3 h-3 rounded-full ${stat.color}`}></div>
                      <span className="text-sm font-medium">{stat.status}</span>
                    </div>
                    <div className="text-right">
                      <span className="text-sm font-bold">{stat.count}</span>
                      <div className="w-20 bg-gray-200 rounded-full h-2 mt-1">
                        <div 
                          className={`h-2 rounded-full ${stat.color}`}
                          style={{ width: `${(stat.count / data.totalReports) * 100}%` }}
                        ></div>
                      </div>
                    </div>
                  </div>
                ))}
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Propuestas Más Votadas</CardTitle>
                <CardDescription>Iniciativas con mayor apoyo ciudadano</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                {data.topProposals.map((proposal, index) => (
                  <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                    <div className="flex-1">
                      <p className="text-sm font-medium truncate">{proposal.title}</p>
                      <p className="text-xs text-gray-500">{proposal.category}</p>
                    </div>
                    <div className="text-right">
                      <Badge variant="secondary" className="bg-blue-100 text-blue-800">
                        {proposal.votes} votos
                      </Badge>
                    </div>
                  </div>
                ))}
              </CardContent>
            </Card>
          </div>
        </TabsContent>

        <TabsContent value="analytics" className="space-y-6">
          {/* Category Analysis */}
          <Card>
            <CardHeader>
              <CardTitle>Análisis por Categoría</CardTitle>
              <CardDescription>Distribución de reportes por tipo de problema</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              {data.categoryStats.map((category) => (
                <div key={category.category} className="space-y-2">
                  <div className="flex justify-between items-center">
                    <span className="text-sm font-medium">{category.category}</span>
                    <span className="text-sm text-gray-600">
                      {category.count} ({category.percentage}%)
                    </span>
                  </div>
                  <Progress value={category.percentage} className="h-2" />
                </div>
              ))}
            </CardContent>
          </Card>

          {/* Monthly Trends */}
          <Card>
            <CardHeader>
              <CardTitle>Tendencias Mensuales</CardTitle>
              <CardDescription>Evolución de reportes y propuestas por mes</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <div className="grid grid-cols-6 gap-4">
                  {data.monthlyTrends.map((month) => (
                    <div key={month.month} className="text-center">
                      <div className="space-y-2">
                        <div className="bg-blue-100 rounded-lg p-2">
                          <div className="text-xs text-gray-600 mb-1">Reportes</div>
                          <div className="text-lg font-bold text-blue-600">{month.reports}</div>
                        </div>
                        <div className="bg-purple-100 rounded-lg p-2">
                          <div className="text-xs text-gray-600 mb-1">Propuestas</div>
                          <div className="text-lg font-bold text-purple-600">{month.proposals}</div>
                        </div>
                        <div className="text-xs font-medium">{month.month}</div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Performance Metrics */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Tiempo Promedio de Resolución</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-3xl font-bold text-green-600">12.5</div>
                <p className="text-sm text-gray-600">días promedio</p>
                <Progress value={75} className="mt-2" />
                <p className="text-xs text-gray-500 mt-1">25% mejor que el objetivo</p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Satisfacción Ciudadana</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-3xl font-bold text-blue-600">4.2</div>
                <p className="text-sm text-gray-600">de 5.0 estrellas</p>
                <Progress value={84} className="mt-2" />
                <p className="text-xs text-gray-500 mt-1">Basado en 1,247 evaluaciones</p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Participación Activa</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-3xl font-bold text-purple-600">68%</div>
                <p className="text-sm text-gray-600">de usuarios activos</p>
                <Progress value={68} className="mt-2" />
                <p className="text-xs text-gray-500 mt-1">2,847 ciudadanos registrados</p>
              </CardContent>
            </Card>
          </div>
        </TabsContent>

        <TabsContent value="activity" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle>Actividad Reciente</CardTitle>
              <CardDescription>Últimas acciones en la plataforma</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {data.recentActivity.map((activity, index) => (
                  <div key={index} className="flex items-center justify-between p-4 border rounded-lg hover:bg-gray-50">
                    <div className="flex items-center gap-4">
                      <div className={`w-2 h-2 rounded-full ${
                        activity.type === 'Reporte' ? 'bg-blue-500' : 'bg-purple-500'
                      }`}></div>
                      <div>
                        <p className="text-sm font-medium">{activity.title}</p>
                        <p className="text-xs text-gray-500">{activity.type} • {activity.time}</p>
                      </div>
                    </div>
                    <Badge 
                      variant="secondary"
                      className={
                        activity.status === 'Nuevo' ? 'bg-blue-100 text-blue-800' :
                        activity.status === 'Aprobada' ? 'bg-green-100 text-green-800' :
                        activity.status === 'En proceso' ? 'bg-yellow-100 text-yellow-800' :
                        activity.status === 'Resuelto' ? 'bg-gray-100 text-gray-800' :
                        'bg-purple-100 text-purple-800'
                      }
                    >
                      {activity.status}
                    </Badge>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Real-time Updates */}
          <Card>
            <CardHeader>
              <CardTitle>Actualizaciones en Tiempo Real</CardTitle>
              <CardDescription>Métricas que se actualizan automáticamente</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-4">
                  <h4 className="font-medium">Reportes Hoy</h4>
                  <div className="text-2xl font-bold text-blue-600">23</div>
                  <Progress value={76} className="h-2" />
                  <p className="text-xs text-gray-500">76% del promedio diario</p>
                </div>
                <div className="space-y-4">
                  <h4 className="font-medium">Votos Hoy</h4>
                  <div className="text-2xl font-bold text-purple-600">156</div>
                  <Progress value={89} className="h-2" />
                  <p className="text-xs text-gray-500">89% del promedio diario</p>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
