"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group'
import { Alert, AlertDescription } from '@/components/ui/alert'

interface ReportData {
  status: string
  category: string
  description: string
  location: string
  evidence: File[]
}

export function ReportForm() {
  const [formData, setFormData] = useState<ReportData>({
    status: '',
    category: '',
    description: '',
    location: '',
    evidence: []
  })
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submitSuccess, setSubmitSuccess] = useState(false)
  const [errors, setErrors] = useState<Record<string, string>>({})

  const statusOptions = [
    { value: 'urgent', label: 'Urgente', color: 'text-red-600' },
    { value: 'important', label: 'Importante', color: 'text-orange-600' },
    { value: 'moderate', label: 'Moderado', color: 'text-yellow-600' },
    { value: 'low', label: 'Bajo', color: 'text-green-600' }
  ]

  const categoryOptions = [
    'Infraestructura',
    'Seguridad',
    'Servicios Públicos',
    'Medio Ambiente',
    'Transporte',
    'Salud',
    'Educación',
    'Otros'
  ]

  const validateForm = () => {
    const newErrors: Record<string, string> = {}

    if (!formData.status) newErrors.status = 'Selecciona el nivel de prioridad'
    if (!formData.category) newErrors.category = 'Selecciona una categoría'
    if (!formData.description.trim()) newErrors.description = 'Describe el problema'
    if (formData.description.length < 20) newErrors.description = 'La descripción debe tener al menos 20 caracteres'
    if (!formData.location.trim()) newErrors.location = 'Indica la ubicación'

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(e.target.files || [])
    const validFiles = files.filter(file => {
      const isValidType = file.type.startsWith('image/') || file.type.startsWith('video/')
      const isValidSize = file.size <= 10 * 1024 * 1024 // 10MB
      return isValidType && isValidSize
    })

    setFormData(prev => ({
      ...prev,
      evidence: [...prev.evidence, ...validFiles].slice(0, 5) // Max 5 files
    }))
  }

  const removeFile = (index: number) => {
    setFormData(prev => ({
      ...prev,
      evidence: prev.evidence.filter((_, i) => i !== index)
    }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) return

    setIsSubmitting(true)
    
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 2000))
    
    setIsSubmitting(false)
    setSubmitSuccess(true)
    
    // Reset form after success
    setTimeout(() => {
      setFormData({
        status: '',
        category: '',
        description: '',
        location: '',
        evidence: []
      })
      setSubmitSuccess(false)
    }, 3000)
  }

  if (submitSuccess) {
    return (
      <Card className="max-w-2xl mx-auto">
        <CardContent className="pt-6">
          <Alert className="border-green-200 bg-green-50">
            <AlertDescription className="text-green-800">
              ¡Reporte enviado exitosamente! Tu reporte ha sido registrado y será revisado por las autoridades correspondientes.
            </AlertDescription>
          </Alert>
        </CardContent>
      </Card>
    )
  }

  return (
    <Card className="max-w-2xl mx-auto">
      <CardHeader>
        <CardTitle>Reportar Problema Comunitario</CardTitle>
        <CardDescription>
          Ayúdanos a mejorar tu comunidad reportando problemas que requieren atención
        </CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Status Selection */}
          <div className="space-y-3">
            <Label className="text-base font-medium">Nivel de Prioridad</Label>
            <RadioGroup
              value={formData.status}
              onValueChange={(value) => setFormData(prev => ({ ...prev, status: value }))}
              className="grid grid-cols-2 gap-4"
            >
              {statusOptions.map((option) => (
                <div key={option.value} className="flex items-center space-x-2">
                  <RadioGroupItem value={option.value} id={option.value} />
                  <Label htmlFor={option.value} className={`${option.color} font-medium`}>
                    {option.label}
                  </Label>
                </div>
              ))}
            </RadioGroup>
            {errors.status && <p className="text-sm text-red-600">{errors.status}</p>}
          </div>

          {/* Category Selection */}
          <div className="space-y-2">
            <Label htmlFor="category">Categoría del Problema</Label>
            <Select value={formData.category} onValueChange={(value) => setFormData(prev => ({ ...prev, category: value }))}>
              <SelectTrigger>
                <SelectValue placeholder="Selecciona una categoría" />
              </SelectTrigger>
              <SelectContent>
                {categoryOptions.map((category) => (
                  <SelectItem key={category} value={category}>
                    {category}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
            {errors.category && <p className="text-sm text-red-600">{errors.category}</p>}
          </div>

          {/* Location */}
          <div className="space-y-2">
            <Label htmlFor="location">Ubicación</Label>
            <Input
              id="location"
              placeholder="Ej: Calle 5 de Mayo #123, Colonia Centro"
              value={formData.location}
              onChange={(e) => setFormData(prev => ({ ...prev, location: e.target.value }))}
            />
            {errors.location && <p className="text-sm text-red-600">{errors.location}</p>}
          </div>

          {/* Description */}
          <div className="space-y-2">
            <Label htmlFor="description">Descripción del Problema</Label>
            <Textarea
              id="description"
              placeholder="Describe detalladamente el problema que observaste..."
              rows={4}
              value={formData.description}
              onChange={(e) => setFormData(prev => ({ ...prev, description: e.target.value }))}
            />
            <p className="text-xs text-gray-500">
              {formData.description.length}/500 caracteres
            </p>
            {errors.description && <p className="text-sm text-red-600">{errors.description}</p>}
          </div>

          {/* File Upload */}
          <div className="space-y-2">
            <Label htmlFor="evidence">Evidencia (Fotos/Videos)</Label>
            <Input
              id="evidence"
              type="file"
              multiple
              accept="image/*,video/*"
              onChange={handleFileUpload}
              className="cursor-pointer"
            />
            <p className="text-xs text-gray-500">
              Máximo 5 archivos, 10MB cada uno. Formatos: JPG, PNG, MP4, MOV
            </p>
            
            {/* File Preview */}
            {formData.evidence.length > 0 && (
              <div className="grid grid-cols-2 gap-2 mt-3">
                {formData.evidence.map((file, index) => (
                  <div key={index} className="relative bg-gray-100 p-2 rounded-lg">
                    <p className="text-xs truncate">{file.name}</p>
                    <p className="text-xs text-gray-500">{(file.size / 1024 / 1024).toFixed(1)} MB</p>
                    <Button
                      type="button"
                      variant="destructive"
                      size="sm"
                      className="absolute top-1 right-1 h-6 w-6 p-0"
                      onClick={() => removeFile(index)}
                    >
                      ×
                    </Button>
                  </div>
                ))}
              </div>
            )}
          </div>

          <Button 
            type="submit" 
            className="w-full bg-blue-600 hover:bg-blue-700"
            disabled={isSubmitting}
          >
            {isSubmitting ? 'Enviando...' : 'Enviar Reporte'}
          </Button>
        </form>
      </CardContent>
    </Card>
  )
}
