"use client"

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Progress } from '@/components/ui/progress'
import { Alert, AlertDescription } from '@/components/ui/alert'

interface Proposal {
  id: string
  title: string
  description: string
  category: string
  author: string
  date: string
  votes: number
  status: 'active' | 'approved' | 'rejected' | 'implemented'
  budget?: number
  timeline?: string
}

const mockProposals: Proposal[] = [
  {
    id: '1',
    title: 'Instalación de ciclovías en Av. Reforma',
    description: 'Propuesta para crear carriles exclusivos para bicicletas que conecten el centro con las zonas residenciales, promoviendo el transporte sustentable.',
    category: 'Transporte',
    author: 'María González',
    date: '2024-01-10',
    votes: 156,
    status: 'active',
    budget: 2500000,
    timeline: '6 meses'
  },
  {
    id: '2',
    title: 'Programa de recolección selectiva de basura',
    description: 'Implementar un sistema de separación de residuos con contenedores específicos y rutas de recolección diferenciadas.',
    category: 'Medio Ambiente',
    author: 'Carlos Ruiz',
    date: '2024-01-08',
    votes: 89,
    status: 'approved',
    budget: 800000,
    timeline: '3 meses'
  },
  {
    id: '3',
    title: 'Mejora de iluminación en parques públicos',
    description: 'Instalación de luminarias LED solares en todos los parques de la zona para mejorar la seguridad nocturna.',
    category: 'Seguridad',
    author: 'Ana Martínez',
    date: '2024-01-05',
    votes: 203,
    status: 'implemented',
    budget: 1200000,
    timeline: '4 meses'
  },
  {
    id: '4',
    title: 'Centro comunitario para adultos mayores',
    description: 'Construcción de un espacio dedicado a actividades recreativas y de salud para la población de la tercera edad.',
    category: 'Servicios Públicos',
    author: 'Roberto Silva',
    date: '2024-01-03',
    votes: 67,
    status: 'active',
    budget: 5000000,
    timeline: '12 meses'
  }
]

const statusColors = {
  active: 'bg-blue-100 text-blue-800',
  approved: 'bg-green-100 text-green-800',
  rejected: 'bg-red-100 text-red-800',
  implemented: 'bg-purple-100 text-purple-800'
}

const statusLabels = {
  active: 'Activa',
  approved: 'Aprobada',
  rejected: 'Rechazada',
  implemented: 'Implementada'
}

export function ProposalSystem() {
  const [proposals, setProposals] = useState<Proposal[]>(mockProposals)
  const [activeTab, setActiveTab] = useState('browse')
  const [newProposal, setNewProposal] = useState({
    title: '',
    description: '',
    category: '',
    budget: '',
    timeline: ''
  })
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submitSuccess, setSubmitSuccess] = useState(false)

  const handleVote = (proposalId: string) => {
    setProposals(prev => prev.map(proposal => 
      proposal.id === proposalId 
        ? { ...proposal, votes: proposal.votes + 1 }
        : proposal
    ))
  }

  const handleSubmitProposal = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSubmitting(true)

    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 2000))

    const proposal: Proposal = {
      id: Date.now().toString(),
      title: newProposal.title,
      description: newProposal.description,
      category: newProposal.category,
      author: 'Usuario Actual',
      date: new Date().toISOString().split('T')[0],
      votes: 1,
      status: 'active',
      budget: newProposal.budget ? parseInt(newProposal.budget) : undefined,
      timeline: newProposal.timeline || undefined
    }

    setProposals(prev => [proposal, ...prev])
    setNewProposal({
      title: '',
      description: '',
      category: '',
      budget: '',
      timeline: ''
    })
    setIsSubmitting(false)
    setSubmitSuccess(true)
    
    setTimeout(() => setSubmitSuccess(false), 3000)
  }

  const getVotePercentage = (votes: number) => {
    const maxVotes = Math.max(...proposals.map(p => p.votes))
    return (votes / maxVotes) * 100
  }

  return (
    <div className="space-y-6">
      <Tabs value={activeTab} onValueChange={setActiveTab}>
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="browse">Explorar Propuestas</TabsTrigger>
          <TabsTrigger value="create">Crear Propuesta</TabsTrigger>
        </TabsList>

        <TabsContent value="browse" className="space-y-6">
          {/* Statistics */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <Card>
              <CardContent className="pt-6">
                <div className="text-center">
                  <p className="text-3xl font-bold text-blue-600">
                    {proposals.filter(p => p.status === 'active').length}
                  </p>
                  <p className="text-sm text-gray-600">Propuestas Activas</p>
                </div>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="pt-6">
                <div className="text-center">
                  <p className="text-3xl font-bold text-green-600">
                    {proposals.filter(p => p.status === 'approved').length}
                  </p>
                  <p className="text-sm text-gray-600">Aprobadas</p>
                </div>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="pt-6">
                <div className="text-center">
                  <p className="text-3xl font-bold text-purple-600">
                    {proposals.filter(p => p.status === 'implemented').length}
                  </p>
                  <p className="text-sm text-gray-600">Implementadas</p>
                </div>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="pt-6">
                <div className="text-center">
                  <p className="text-3xl font-bold text-gray-600">
                    {proposals.reduce((sum, p) => sum + p.votes, 0)}
                  </p>
                  <p className="text-sm text-gray-600">Total Votos</p>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Proposals List */}
          <div className="grid gap-6">
            {proposals.map((proposal) => (
              <Card key={proposal.id} className="hover:shadow-lg transition-shadow">
                <CardHeader>
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <CardTitle className="text-xl mb-2">{proposal.title}</CardTitle>
                      <CardDescription className="text-base">
                        {proposal.description}
                      </CardDescription>
                    </div>
                    <Badge className={statusColors[proposal.status]}>
                      {statusLabels[proposal.status]}
                    </Badge>
                  </div>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm">
                    <div>
                      <p className="font-medium text-gray-700">Categoría</p>
                      <p>{proposal.category}</p>
                    </div>
                    <div>
                      <p className="font-medium text-gray-700">Autor</p>
                      <p>{proposal.author}</p>
                    </div>
                    <div>
                      <p className="font-medium text-gray-700">Fecha</p>
                      <p>{new Date(proposal.date).toLocaleDateString('es-MX')}</p>
                    </div>
                  </div>

                  {(proposal.budget || proposal.timeline) && (
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                      {proposal.budget && (
                        <div>
                          <p className="font-medium text-gray-700">Presupuesto Estimado</p>
                          <p>${proposal.budget.toLocaleString('es-MX')}</p>
                        </div>
                      )}
                      {proposal.timeline && (
                        <div>
                          <p className="font-medium text-gray-700">Tiempo Estimado</p>
                          <p>{proposal.timeline}</p>
                        </div>
                      )}
                    </div>
                  )}

                  <div className="space-y-2">
                    <div className="flex items-center justify-between">
                      <span className="text-sm font-medium">Apoyo ciudadano</span>
                      <span className="text-sm text-gray-600">{proposal.votes} votos</span>
                    </div>
                    <Progress value={getVotePercentage(proposal.votes)} className="h-2" />
                  </div>

                  <div className="flex items-center justify-between pt-4 border-t">
                    <div className="flex items-center gap-4">
                      <Button
                        size="sm"
                        onClick={() => handleVote(proposal.id)}
                        className="bg-blue-600 hover:bg-blue-700"
                        disabled={proposal.status !== 'active'}
                      >
                        👍 Apoyar ({proposal.votes})
                      </Button>
                    </div>
                    <div className="text-xs text-gray-500">
                      {proposal.status === 'active' && 'Votación abierta'}
                      {proposal.status === 'approved' && 'En proceso de implementación'}
                      {proposal.status === 'implemented' && 'Proyecto completado'}
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="create" className="space-y-6">
          <Card className="max-w-2xl mx-auto">
            <CardHeader>
              <CardTitle>Crear Nueva Propuesta</CardTitle>
              <CardDescription>
                Comparte tu idea para mejorar la comunidad y obtén el apoyo de otros ciudadanos
              </CardDescription>
            </CardHeader>
            <CardContent>
              {submitSuccess && (
                <Alert className="mb-6 border-green-200 bg-green-50">
                  <AlertDescription className="text-green-800">
                    ¡Propuesta creada exitosamente! Tu propuesta está ahora disponible para votación.
                  </AlertDescription>
                </Alert>
              )}

              <form onSubmit={handleSubmitProposal} className="space-y-6">
                <div className="space-y-2">
                  <Label htmlFor="title">Título de la Propuesta</Label>
                  <Input
                    id="title"
                    placeholder="Ej: Instalación de nuevos semáforos"
                    value={newProposal.title}
                    onChange={(e) => setNewProposal(prev => ({ ...prev, title: e.target.value }))}
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="category">Categoría</Label>
                  <Input
                    id="category"
                    placeholder="Ej: Transporte, Seguridad, Medio Ambiente"
                    value={newProposal.category}
                    onChange={(e) => setNewProposal(prev => ({ ...prev, category: e.target.value }))}
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="description">Descripción Detallada</Label>
                  <Textarea
                    id="description"
                    placeholder="Describe tu propuesta, beneficios esperados, y cómo mejoraría la comunidad..."
                    rows={6}
                    value={newProposal.description}
                    onChange={(e) => setNewProposal(prev => ({ ...prev, description: e.target.value }))}
                    required
                  />
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="budget">Presupuesto Estimado (MXN)</Label>
                    <Input
                      id="budget"
                      type="number"
                      placeholder="1000000"
                      value={newProposal.budget}
                      onChange={(e) => setNewProposal(prev => ({ ...prev, budget: e.target.value }))}
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="timeline">Tiempo de Implementación</Label>
                    <Input
                      id="timeline"
                      placeholder="Ej: 6 meses"
                      value={newProposal.timeline}
                      onChange={(e) => setNewProposal(prev => ({ ...prev, timeline: e.target.value }))}
                    />
                  </div>
                </div>

                <Button 
                  type="submit" 
                  className="w-full bg-blue-600 hover:bg-blue-700"
                  disabled={isSubmitting}
                >
                  {isSubmitting ? 'Creando Propuesta...' : 'Crear Propuesta'}
                </Button>
              </form>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
